.projects__main-container{
    width:100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-top: 1px solid #C7C7C7;
    border-bottom: 1px solid rgb(255, 255, 255);
    
    background: -moz-linear-gradient(102deg, #0D47A1 2%, #0C83C8 37.1%, #FFFFFF 38.1%, #C7C7C7 39.1%, #C7C7C7 62.5%, #FFFFFF 63.5%, #0C83C8 64.5%, #0D47A1 94%);
    background: -webkit-linear-gradient(102deg, #0D47A1 2%, #0C83C8 37.1%, #FFFFFF 38.1%, #C7C7C7 39.1%, #C7C7C7 62.5%, #FFFFFF 63.5%, #0C83C8 64.5%, #0D47A1 94%);
    background: -o-linear-gradient(102deg, #0D47A1 2%, #0C83C8 37.1%, #FFFFFF 38.1%, #C7C7C7 39.1%, #C7C7C7 62.5%, #FFFFFF 63.5%, #0C83C8 64.5%, #0D47A1 94%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#0D47A1', endColorstr='#0D47A1', GradientType=0);
    background: -ms-linear-gradient(102deg, #0D47A1 2%, #0C83C8 37.1%, #FFFFFF 38.1%, #C7C7C7 39.1%, #C7C7C7 62.5%, #FFFFFF 63.5%, #0C83C8 64.5%, #0D47A1 94%);
    background: linear-gradient(102deg, #0D47A1 2%, #0C83C8 37.1%, #FFFFFF 38.1%, #C7C7C7 39.1%, #C7C7C7 62.5%, #FFFFFF 63.5%, #0C83C8 64.5%, #0D47A1 94%);
    background-attachment: fixed;
}
.projects__sub_container{
    width: 80%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background:linear-gradient(rgba(0, 0, 0, 0.762), rgba(0, 0, 0, 0.518));
    margin: 3rem 0 6rem 0;
    border-radius:20px ;
}

.projects__second_projects{
    padding: 5rem 0;
    max-width: 1150px;
}
.projects__second_projects{
    width:80%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
}
.second_project_text{
    flex: 1;
    height: 300px;
    padding: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: rgba(0, 0, 0, 0.606);
    border-radius: 20px; 
    box-shadow: 0 0 3px rgb(161, 161, 161);
    transition: 0.3s;
}
.second_project_text:hover{
    transform: scale(1.02);
}

.second_project_img{
    height:300px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    transition: 0.3s;
    margin-left:10px;
    box-shadow: 0 0 10px black;
    border: 1px solid rgb(72, 72, 72);
}
.second_project_img span img{
    object-fit: cover;
    width: 100%;
    height: 100%;
    border-radius: 20px; 
}
#dark .projects__main-container{
    background: -moz-linear-gradient(102deg, #FFC107 2%, #FFEB3B 38%, #FFFFFF 39%, #000000 40%, #000000 64%, #FFFFFF 65%, #FFEB3B 66%, #FFC107 94%);
    background: -webkit-linear-gradient(102deg, #FFC107 2%, #FFEB3B 38%, #FFFFFF 39%, #000000 40%, #000000 64%, #FFFFFF 65%, #FFEB3B 66%, #FFC107 94%);
    background: -o-linear-gradient(102deg, #FFC107 2%, #FFEB3B 38%, #FFFFFF 39%, #000000 40%, #000000 64%, #FFFFFF 65%, #FFEB3B 66%, #FFC107 94%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#FFC107', endColorstr='#FFC107', GradientType=0);
    background: -ms-linear-gradient(102deg, #FFC107 2%, #FFEB3B 38%, #FFFFFF 39%, #000000 40%, #000000 64%, #FFFFFF 65%, #FFEB3B 66%, #FFC107 94%);
    background: linear-gradient(102deg, #FFC107 2%, #FFEB3B 38%, #FFFFFF 39%, #000000 40%, #000000 64%, #FFFFFF 65%, #FFEB3B 66%, #FFC107 94%);
    background-attachment: fixed;
}
.projects__sub_container{
    background:linear-gradient(rgba(0, 0, 0, 0.806), rgba(0, 0, 0, 0.642));
}
.github_icon{
    width:100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
@media screen and (min-width:2000px) {
    .second_project_text{
        padding: 30px 90px
    }
}
@media screen and (min-width:1440px) {
    .projects__second_projects{
        width: 68%;
    }
}
@media screen and (max-width:1000px) {
    .projects__second_projects{
        flex-direction: column;
    }
    .second_project_text{
     margin-bottom: 20px;
    }
}
@media screen and (max-width:850px) {
    .projects__sub_container {
        width: 90%;
    }
    .first_project_text{
        width: 90%;
    }
}
@media screen and (max-width:500px) {
    .projects__second_projects{
        width: 90%;
    }
    .second_project_text h2{
        font-size: 22px;
    }
    .second_project_text div p{
      font-size: 16px;
       }
       .second_project_img{
        height: 500px;
        margin: 0;
       }
       .second_project_text{
        padding: 10px;
       }
       .contact__contact h1{
        margin: 8px 2px 8px 0;
        font-size: 15px !important;
       }
       .contact__contact h2{
        margin-left: 5px;
        padding-left: 5px;
       }
       .contact__contact p{
        font-size: 13px !important;
       }
       .contact__links h1{
        font-size: 15px !important;
       }     
}
